export default {
	data() {
		return {
			dataSource: [],
			TabCur: 0,
			scrollLeft: 0
		};
	},
	onLoad() {
		this.getData()
	},
	onShow() {
		//this.getData()
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		getData() {
			uni.request({
				url: 'https://node.pandaps.cn/reptile/index',
				success: (res) => {
					console.log(res.data);
					this.dataSource = res.data;
				}
			});
		},
		toDetil(e) {
			console.log(e)
			uni.navigateTo({
				url: '/pages/detil/detil?id=' + e
			});
		}
	}
}
